.appmanage{
    background-color: #ebf0f5;
    margin-left: 5px;
    margin-right: 8px;
    
    &__title{
        height: 50px;
        line-height: 50px;
        padding-left: 15px;
        font-size: 18px;
        color: #000;
        border-bottom: 1px solid #b8cee0;
    }

    &__btn{
        margin: 10px;
    }

    &__table{
       background-color: #fff;
    }

    &__action{
        > span{
            color: #1890ff; 
            cursor: pointer;
            margin-right: 10px;
        }
    }
    &__tag{
        cursor: default!important;
    }
}