.mainhead{
    display: flex;
    height: 50px;
    line-height: 50px;
    justify-content: flex-end;
    align-items: center;
    color: #666;
    font-size: 12px;
    > div:nth-child(1){
        width: 140px;
    }
    > div:nth-child(2){
        cursor: pointer;
        width: 80px;
        padding-left: 40px;
        border-left: 1px solid #e8e8e8;
        background: url(../images/info.png) no-repeat 18px center;
    }
    > div:nth-child(3){
        cursor: pointer;
        width: 120px;
        padding-left: 50px;
        border-left: 1px solid #e8e8e8;
        background: url(../images/logout.png) no-repeat 25px center;
    }
}