.login{
    display: flex;
    height: 100%;
    background-image: url(../images/bj.png);
    background-size: cover;
    align-items: center;
    justify-content: center;

    &__carousel{
        background-color: #ffffffcb;
        width: 420px;
        height: 450px; 
        border-radius: 30px 0px 0px 30px;
        box-shadow: 5px 5px 5px #888888;
    }

    &__img{
        width: 400px!important;
        height: 300px;
        padding: 1px;
        border-radius: 10px;
        margin-top: 70px;
        margin-left: 20px;
    }

    &__content{
        background-color: #ffffffcb;
        width: 350px;
        height: 450px;
        border-radius: 0px 30px 30px 0px;
        box-shadow: 5px 5px 5px #888888;
    }

    &__head{
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        &--logo{
            width: 80px;
            height: 45px;
            margin-left: 60px;
            margin-top: 60px;
        }
    
        &--enTitle{
            color: #5ba79c;
            font-size: 10px;
            margin-top: 60px;
            margin-bottom: 3px;
        }
    
        &--title{
            color: #494949;
            font-size: 18px;
        }
    }

    &__user{
        width: 300px;
        margin-left: 30px;
        margin-top: 30px;
    }

    &__password{
        width: 300px;
        margin-left: 30px;
        margin-top: 20px;
    }

    &__btn{
        width: 280px;
        margin-top: 70px;
        margin-left: 40px;
    }

}

.ant-input-group-addon {
    padding: 8px 11px!important;
}